
/*================================HEADER===================================*/

.page-header{
    margin-bottom: 136px;
}
.page-header__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.navigation__list {
    display: none;
}

.header__button {
    display: none;
    width: 160px;
    height: 44px;
    border-radius: 22px;
    padding-top: 12px;
    padding-bottom: 12px;
    cursor: pointer;
    border: none;
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.31;
    align-items: center;
    text-align: center;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

    background-color: #fff;
    color: var(--basic-title-color);

    &:hover {
        background-color: #ECEBEB;
    }
}



@media screen and (min-width: 768px) {
.page-header {
        margin-bottom: 20px;
    }
    .page-header__container {
        justify-content: space-between;
    }

    .header-buttons__container {
        display: flex;
        align-items: baseline;
    }

    .header__button {
        display: block;
        margin-left: 32px;
    }
}




@media screen and (min-width: 1280px) {
.page-header {
        margin-bottom: 60px;
    }
    .page-header__container {
        display: flex;
        justify-content: space-between;

    }

    .navigation__list {
        display: flex;
        margin-left: 128px;
    }

    .navigation__item:not(:last-child) {
        margin-right: 35px;
    }

    .navigation__link {
        font-family: 'DM Sans';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 1.31;
        color: #FFFFFF;
        transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

        &:hover,
        &:focus {
            color: var(--basic-title-color);
        }
    }

    .mobile-menu__open-button {
        display: none;
    }

    .header__button {
        display: block;
        margin-left: 141px;
    }
}








/*================================= HERO ===============================*/
  .hero__item-numbers-text {
      font-family: 'DM Sans';
      font-style: normal;
      font-weight: 400;
      font-size: 8px;
      line-height: 1.2;
      text-align: right;
      color: #FFFFFF;
  }
.hero__text {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 8px;
    line-height: 150%;
    color: #FFFFFF;
}

.hero {
 
    width: 100%;
    background-color: var(--background-cream);
}
.hero__title {
    align-items: center;
    margin-bottom: 20px;
    font-family: 'Titan One';
    font-size: 26px;
    line-height: 1.1;
    letter-spacing: 0.04em;
    color: #FFFFFF;
    text-transform: uppercase;
}

.hero__title-part {
    font-size: 48px;
    color: var(--basic-title-color);
}

.hero__menu {
    display: flex;
    position: relative;
}

.hero__item {
    text-align: center;
    padding: 8px 14px;
    height: 24px;
    min-width: 61.2px;
    font-family: 'DM Sans';
    font-size: 8px;
    line-height: 1.25;
    background: var(--basic-title-color);
    border-radius: 20px;

    &:not(:last-child) {
        margin-right: 8px;
    }

}

.hero__item-white {
    background-color: #FFFFFF;
    color: var(--basic-title-color);
}

.hero__link-red {
    color: var(--basic-title-color);
}

.hero__link-wite {
    color: #FFFFFF;


}

